import {Component} from '@angular/core'
import {ROUTER_DIRECTIVES, RouterConfig, Route} from '@angular/router'
import {HTTP_PROVIDERS} from '@angular/http'

import {SlimLoadingBar} from 'ng2-slim-loading-bar/ng2-slim-loading-bar';

@Component({
    selector: 'app',
    directives: [ROUTER_DIRECTIVES, SlimLoadingBar],
    template: '<ng2-slim-loading-bar></ng2-slim-loading-bar><router-outlet></router-outlet>',
    providers: [HTTP_PROVIDERS]
})
export class AppComponent {
    title = "Hello Webpack";
}


